  width: 100%;
  text-align: right;
  justify-content: flex-end;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  overflow-wrap: break-word;
}

.chatBox p {
  padding: 0.5rem 1rem;
  font-family: 'Segoe UI';
  width: 60%;
  color: black;
  color: white;
  border-radius: 10px;
  border-top-right-radius: 0;
  box-shadow: 1px 2px 3px rgb(0 0 0 / 18%);
}

.chatBox-left {
  text-align: left;
  justify-content: flex-start;
