}

.rejectButton {
  border: 1px solid #707070;
  background-color: #707070;
}

.acceptButton:hover {
  color: green;
  background: white;
}

.rejectButton:hover {
  color: red;
  background: white;
}

.acceptButton:hover, .acceptButton:focus, .acceptButton:active, .rejectButton:hover, .rejectButton:focus, .rejectButton:active {
  outline: none;
}

.postQuotation>button {
  background-color: #02044a;
}

